import React, { useState } from 'react';
import { Row, Col, Steps, Tabs, TabsProps, Typography, CheckboxOptionType, Checkbox } from 'antd';
import {SearchOutlined} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

export const Welcome: React.FC = () => {
    const navigate = useNavigate();

    const [currentTab, setCurrentTab] = useState('1');
    const [currentCouncillorStep, setCurrentCouncillorStep] = useState(0);
    const [currentAgendaStep, setCurrentAgendaStep] = useState(0);

    const onCouncillorStep = (step: number) => {
        setCurrentCouncillorStep(step);
        if(step === 3) {
            navigate('/councillor')
        }
    };
    const onAgendaStep = (step: number) => {
        setCurrentAgendaStep(step);
        if(step === 2) {
            navigate('/agenda')
        }
    };

    const items: TabsProps['items'] = [
        {
            key: '1',
            label: '',
            children: 
                <Steps
                    type="navigation"
                    current={currentCouncillorStep}
                    onChange={onCouncillorStep}
                    className="site-navigation-steps"
                    items={[
                        {
                            status: 'wait',
                            title: '',
                        },
                        {
                            status: 'wait',
                            title: '',
                        },
                        {
                            status: 'wait',
                            title: '',
                        },
                        {
                            status: 'wait',
                            title: '',
                            icon: <SearchOutlined />
                        }
                    ]}
                />
        },
        {
            key: '2',
            label: '',
            children:
                <Steps
                    type="navigation"
                    current={currentAgendaStep}
                    onChange={onAgendaStep}
                    className="site-navigation-steps"
                    items={[
                        {
                            status: 'wait',
                            title: '',
                        },
                        {
                            status: 'wait',
                            title: '',
                        },
                        {
                            status: 'wait',
                            title: '',
                            icon: <SearchOutlined />
                        }
                    ]}
                />
        }
    ];

    const topicKyoiku: CheckboxOptionType<string>[] = [
        { label: '', value: 'Apple', className: 'label-1' },
        { label: '', value: 'Pear', className: 'label-2' },
    ];
    const topicKoyo: CheckboxOptionType<string>[] = [
        { label: '', value: 'Apple', className: 'label-1' },
        { label: '', value: 'Pear', className: 'label-2' },
        { label: '', value: 'Orange', className: 'label-2' },
    ];
    const topicInfra: CheckboxOptionType<string>[] = [
        { label: '', value: 'Apple', className: 'label-1' },
        { label: '', value: 'Pear', className: 'label-2' },
        { label: '', value: 'Orange', className: 'label-2' },
        { label: '', value: 'Orange', className: 'label-2' },
    ];
    const regions: CheckboxOptionType<string>[] = [
        { label: '', value: 'nishi', className: 'label-3' },
        { label: '', value: 'kita', className: 'label-1' },
        { label: '', value: 'omiya', className: 'label-1' },
        { label: '', value: 'minuma', className: 'label-1' },
        { label: '', value: 'chuo', className: 'label-1' },
        { label: '', value: 'sakura', className: 'label-1' },
        { label: '', value: 'urawa', className: 'label-3' },
        { label: '', value: 'minami', className: 'label-3' },
        { label: '', value: 'midori', className: 'label-2' },
        { label: '', value: 'iwatsuki', className: 'label-2' },
    ];
    const parties: CheckboxOptionType<string>[] = [
        { label: '', value: 'zimintoshigidan', className: 'label-1' },
        { label: '', value: 'ishin', className: 'label-2' },
        { label: '', value: 'rikken', className: 'label-3' },
        { label: '', value: 'kokumin', className: 'label-3' },
        { label: '', value: 'komei', className: 'label-3' },
        { label: '', value: 'sansei', className: 'label-3' },
        { label: '', value: 'reiwa', className: 'label-3' },
        { label: '', value: 'kyousan', className: 'label-3' },
        { label: '', value: 'hosyu', className: 'label-3' },
        { label: '', value: 'shamin', className: 'label-3' },
        { label: '', value: 'mirai', className: 'label-3' },
        { label: '', value: 'musyozoku', className: 'label-3' },
    ];

    const councillorTopic = () => {
        return (
            <div>
                <Typography.Title level={5}></Typography.Title>
                {
                    topicKyoiku.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
                <Typography.Title level={5}></Typography.Title>
                {
                    topicKoyo.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
                <Typography.Title level={5}></Typography.Title>
                {
                    topicInfra.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
            </div>
        )
    };
    const councillorRegion = () => {
        return regions.map(option => (
            <Checkbox key={option.value} className={option.className}>
                {option.label}
            </Checkbox>
        ));
    };
    const councillorParty = () => {
        return parties.map(option => (
            <Checkbox key={option.value} className={option.className}>
                {option.label}
            </Checkbox>
        ));
    };

    const agendaTopic = () => {
        return (
            <div>
                <Typography.Title level={5}></Typography.Title>
                {
                    topicKyoiku.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
                <Typography.Title level={5}></Typography.Title>
                {
                    topicKoyo.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
                <Typography.Title level={5}></Typography.Title>
                {
                    topicInfra.map(option => (
                        <Checkbox key={option.value} className={option.className}>
                            {option.label}
                        </Checkbox>
                    ))
                }
            </div>
        )
    };
    const agendaRegions = () => {
        return regions.map(option => (
            <Checkbox key={option.value} className={option.className}>
                {option.label}
            </Checkbox>
        ));
    };

    return (
        <div>
            <Row style={{marginBottom: 24}}>
                <Col span={24}>
                    <Tabs defaultActiveKey="1" items={items} onChange={setCurrentTab} />
                </Col>
            </Row>
            <Row>
                {currentTab === '1' ? 
                    currentCouncillorStep === 0 ? councillorTopic() :
                    currentCouncillorStep === 1 ? councillorRegion() :
                    currentCouncillorStep === 2 ? councillorParty() :
                    null
                 :  currentAgendaStep === 0 ? agendaTopic() :
                    currentAgendaStep === 1 ? agendaRegions() :
                    null
                }
            </Row>
        </div>
    );
};